import React from "react"
import { Link } from "gatsby"
import { graphql } from "gatsby"

import Layout from "../components/layout/custom/layout"
// import Image from "../components/image"
import SEO from "../components/layout/custom/seo"

import logo from "../images/Tekken_series_logo_as_of_2012.gif"
function importAll(r) {
  return r.keys().map(r)
}

// const playersImage = importAll(require.context('../images/players', false, /\.(png|jpe?g|svg)$/));

export const query = graphql`
  {
    allPlayersCsv {
      edges {
        node {
          id
          name
          image
          first_appearence
          created_by
          occupation
          fighting_style
          nationality
          created_at
        }
      }
    }
  }
`

const IndexPage = ({ data }) => {
  const players = data.allPlayersCsv.edges

  React.useEffect(() => {
    console.log("hihi")
    // console.log(playersImage);
  }, [])

  return (
    <Layout>
      <SEO title="Home" />

      {/* <!-- ======= Hero Section ======= --> */}
      <section
        id="hero"
        className="d-flex justify-cntent-center align-items-center"
      >
        <div
          id="heroCarousel"
          className="container carousel carousel-fade"
          data-ride="carousel"
        >
          {/* <!-- Slide 1 --> */}
          <div className="carousel-item active">
            <div className="carousel-container">
              <img src={logo} alt="" />
              <h2 className="animated fadeInDown">
                Welcome to <span>The King of Iron Fist Tournament</span>
              </h2>
              <p className="animated fadeInUp">
                Tekken (Japanese: 鉄拳, "Iron Fist") is a Japanese media
                franchise centered on a series of fighting video and arcade
                games developed and published by Bandai Namco Entertainment. The
                franchise also includes film and print adaptations.
              </p>
            </div>
          </div>

          {/* <!-- Slide 2 --> */}
          <div className="carousel-item">
            <div className="carousel-container">
              <h2 className="animated fadeInDown">Lorem Ipsum Dolor</h2>
              <p className="animated fadeInUp">
                Ut velit est quam dolor ad a aliquid qui aliquid. Sequi ea ut et
                est quaerat sequi nihil ut aliquam. Occaecati alias dolorem
                mollitia ut. Similique ea voluptatem. Esse doloremque accusamus
                repellendus deleniti vel. Minus et tempore modi architecto.
              </p>
            </div>
          </div>

          {/* <!-- Slide 3 --> */}
          <div className="carousel-item">
            <div className="carousel-container">
              <h2 className="animated fadeInDown">
                Sequi ea ut et est quaerat
              </h2>
              <p className="animated fadeInUp">
                Ut velit est quam dolor ad a aliquid qui aliquid. Sequi ea ut et
                est quaerat sequi nihil ut aliquam. Occaecati alias dolorem
                mollitia ut. Similique ea voluptatem. Esse doloremque accusamus
                repellendus deleniti vel. Minus et tempore modi architecto.
              </p>
            </div>
          </div>

          <a
            className="carousel-control-prev"
            href="#heroCarousel"
            role="button"
            data-slide="prev"
          >
            <span
              className="carousel-control-prev-icon bx bx-chevron-left"
              aria-hidden="true"
            ></span>
            <span className="sr-only">Previous</span>
          </a>

          <a
            className="carousel-control-next"
            href="#heroCarousel"
            role="button"
            data-slide="next"
          >
            <span
              className="carousel-control-next-icon bx bx-chevron-right"
              aria-hidden="true"
            ></span>
            <span className="sr-only">Next</span>
          </a>
        </div>
      </section>
      {/* <!-- End Hero --> */}

      {/* <div className="hero-waves">
      <svg
        width="100%"
        height="100%"
        version="1.1"
        xmlns="http://www.w3.org/2000/svg"
        className="wave"
      >
        <defs></defs>
        <path id="wave1" d="" />
      </svg>
      <svg
        width="100%"
        height="100%"
        version="1.1"
        xmlns="http://www.w3.org/2000/svg"
        className="wave"
      >
        <defs></defs>
        <path id="wave2" d="" />
      </svg>
    </div> */}

      <main id="main">
        {/* <!-- ======= Team Section ======= --> */}
        <section id="team" className="team">
          <div className="container">
            <div className="section-title" data-aos="zoom-out">
              <h2>The King of Iron Fist Tournament</h2>
              <p>Fighters</p>
            </div>
            <div className="row">
              {players.map((player, i) => {
                const img = require("../images/players/" + player.node.image)

                return (
                  <div
                    key={i}
                    className="col-lg-3 col-md-6 d-flex align-items-stretch"
                  >
                    <Link
                      to={"/player-details?id=" + player.node.id}
                      state={{ modal: true }}
                    >
                      <div className="member" data-aos="fade-up">
                        <div
                          className="member-img"
                          style={{ maxWidth: "255px", background: "#d6d6d8" }}
                        >
                          <img
                            src={img}
                            className="img-fluid zoom"
                            alt=""
                            style={{ maxWidth: "255px",  position:'relative',zIndex:'1000' }}
                          />
                        </div>
                        <div className="member-info">
                          <h4>{player.node.name}</h4>
                          <span>{player.node.occupation}</span>
                        </div>
                      </div>
                    </Link>
                  </div>
                )
              })}
            </div>
          </div>
        </section>
        {/* <!-- End Team Section --> */}
      </main>
      {/* <!-- End #main --> */}
    </Layout>
  )
}
export default IndexPage
